@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    position: relative;
    background-color: v.$cyan;

    &::before {
        position: absolute;
        top: -25rem; // Segun la altura ncesaria del height le damos el mismo top negativo
        left: 0;
        content: ''; // Los Before y After que no tengan asignado un content, aunque sea vacio, no existiran para el navegador
        background-image: url(../img/bg-footer-top-mobile.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center bottom; // X centro Y anclado abajo
        height: 25rem; // Buscamos la altura necesaria para que la curva no se corte con la caja tanto para mobile como para desktop
        width: 100%;
    }

    @include m.phone {
        &::before {
            background-image: url(../img/bg-footer-top-desktop.svg);
        }
    }

    &__grid {
        padding-top: 6rem;
        width: fit-content;
        margin: 0 auto;

        @include m.desktop {
            margin: 0;
            padding-top: 12rem;
            width: auto;            

            display: flex;
            justify-content: space-between;
            gap: 6rem;
        }

    }

    &__newsletter {
        @include m.desktop {
            flex-basis: 62rem;
            order: 1;
        }
    }

    &__newsletter-heading {
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 700;
        color: v.$white;

        @include m.desktop {
            margin-top: 1.8rem;
            font-size: 2.8rem;
            line-height: 1;
        }
    }

    &__text {
        margin-top: 1.6rem;
        font-size: 1.45rem;
        line-height: 1.7;
        color: v.$white;
        max-width: 40rem;

        @include m.desktop {
            margin-top: 2.8rem;
            font-size: 1.7rem;
        }
    }
    
    &__form {
        margin-top: 3.4rem;
        max-width: 40rem;

        display: flex;
        flex-direction: column;
        align-items: flex-end;
        gap: 1.6rem;

        @include m.desktop {
            margin-top: 4.8rem;
            max-width: unset;

            flex-direction: row;
            align-items: stretch;
            gap: 4.6rem;
        }
    }

    &__input {
        border: 0;
        border-radius: .4rem;

        &:nth-child(1) {
            padding: 1.4rem;
            width: 100%;
        }
        

        &:nth-child(2) {
            padding: 1.4rem 4rem;
            background-color: v.$pink;
            font-weight: 700;
            color: v.$white;
            cursor: pointer;

            &:hover {
                background-color: v.$lightPink;
            }
        }

        @include m.desktop {
            &:nth-child(1) {
                padding: 1.6rem;
            }
    
            &:nth-child(2) {
                font-size: 2rem;
                padding: 1.8rem 4rem;
            }
        }
    }

    // CONTENT
    &__content {
        margin-top: 10rem;

        @include m.desktop {
            margin-top: 0;
        }
    }

    &__brand {
        width: 20rem;
        filter: brightness(0) saturate(100%) invert(93%) sepia(0%) hue-rotate(57deg) contrast(110%);

        @include m.desktop {
            width: 28rem;
        }
    }

    &__phone,
    &__email {
        &::before {
            content: '';
            display: inline-block;
            margin-right: 2rem;
            height: 1.9rem;
            width: 1.9rem;
            background-repeat: no-repeat;
            background-size: 1.9rem;
            background-position: center;
            vertical-align: middle;
        }

        font-size: 1.4rem;
        line-height: 1;
        color: v.$white;

        @include m.desktop {
            &::before {
                height: 2.2rem;
                width: 2.2rem;
                background-size: 2.2rem;
            }

            font-size: 1.8rem;
        }
    }

    &__phone {
        margin-top: 4.4rem;

        &::before {
            background-image: url(../img/icon-phone.svg);
        }

        @include m.desktop {
            margin-top: 3.4rem;
        }
    }

    &__email {
        margin-top: 2rem;
        &::before {
            background-image: url(../img/icon-email.svg);
        }

        @include m.desktop {
            margin-top: 2.4rem;
        }
    }
}

.social {
    padding: 5rem 0 4rem 0;
    display: flex;
    gap: 1.8rem;

    @include m.desktop {
        padding: 10rem 0 12rem 0;
        gap: 3rem;
    }

    &__enlace {
        &:hover {
            filter: brightness(0) saturate(100%) invert(55%) sepia(78%) saturate(1886%) hue-rotate(159deg) brightness(103%) contrast(105%);
        }
    }

    &__enlace::before {
        content: '';
        width: 2.3rem;
        height: 2.3rem;
        display: inline-block;
        background-repeat: no-repeat;
        background-size: 2.3rem;

        @include m.desktop {
            height: 3.6rem;
            width: 3.6rem;
            background-size: 3.6rem;
        }
    }
    
    &__enlace[href*="facebook.com"]::before {
        background-image: url(../img/icon-facebook.svg);
    }
    &__enlace[href*="twitter.com"]::before {
        background-image: url(../img/icon-twitter.svg);
    }
    &__enlace[href*="instagram.com"]::before {
        background-image: url(../img/icon-instagram.svg);
    }
    
    &__accesible { 
        display: block;
        width: 1px;
        height: 1px;
        margin: 0;
        border: 0;
        padding: 0;
        overflow: hidden;
        clip: rect(0,0,0,0);
    }
}

.attribution {
    padding: 2rem;
    display: flex;
    gap: 2rem;
    background-color: #00191f;
    color: #e0e0e0;
    justify-content: center;

    &__text0 {
        font-size: 1.3rem;
    }

    &__enlace {
        color: #e0e0e0;

        &:hover {
            color: #ffffff;
        }
    }
}