@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    &__navbar {
        padding: 2.4rem 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include m.phone {
            padding-top: 5rem;
        }

        @include m.desktopxl {
            padding-top: 9.5rem;
        }
    }
     &__brand {
        width: 9.6rem;

        @include m.phone {
            width: 18.3rem;
        }

        @include m.desktopxl {
            width: 27rem;
        }
    }

    &__btn {
        padding: .5rem 1.6rem;
        border: .1rem solid v.$pink;
        border-radius: 15rem;
        font-size: 1rem;
        color: v.$pink;

        &:hover {
            border: .1rem solid v.$lightPink;
            color: v.$lightPink;
        }

        @include m.phone {
            padding: .9rem 2.5rem;
            font-size: 1.5rem;
        }

        @include m.desktopxl { 
            padding: 1.2rem 3.4rem;
            font-size: 2rem;
        }
    }
}