@use 'base/variables' as v;
@use 'base/mixins' as m;

.start {
    margin-top: 8rem;
    padding: 10rem 0 24rem 0;
    
    @include m.phone {
        padding: 25rem 0 32rem 0;
    }
    @include m.desktopxl {
        padding-bottom: 42rem;
    }
    
    &__heading {
        @include m.headingTwo;
    }

    &__btn {
        @include m.btn;
        margin-top: 4rem;

        @include m.desktopxl {
            margin-top: 5rem;
        }
    }
}