@use 'base/variables' as v;
@use 'base/mixins' as m;

.community {
    margin-top: 9rem;

    @include m.desktopxl {
        margin-top: 15rem;
    }

    &__heading {
        margin: 0 auto;
        max-width: 30rem;
        font-family: v.$secondaryFont;
        font-size: 2.4rem;
        line-height: 1.5;
        text-align: center;
        color: v.$cyan;
    }

    &__text {
        @include m.text;
        margin: 2.2rem auto 4.7rem auto;
        max-width: 30rem;
    }

    &__btn {
        @include m.btn;
    }

    @include m.phone {
        &__heading {
            max-width: 70rem;
            font-size: 4rem;
        }

        &__text {
            margin: 2.5rem auto 5.8rem auto;
            max-width: 50rem;
        }
    }

    @include m.desktopxl {
        &__heading {
            max-width: 130rem;
            font-size: 5.8rem;
            line-height: 1.5;
        }

        &__text {
            max-width: 71rem;
        }
    }

    &__image {
        margin: 10rem auto 12rem auto;
        max-width: 93%;

        @include m.desktopxl {
            margin-top: 16rem;
            max-width: 87%;
        }
    }

    &__stats {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 9rem;
    }

    &__stat-icon {
        max-width: 3rem;
    }

    &__stat-number {
        font-size: 6rem;
        font-weight: 700;
        line-height: 1.4;
        color: v.$cyan;
    }

    &__stat:nth-child(2) {
        .community__stat-icon {
            max-width: 2.3rem;
        }

        .community__stat-number {
            font-size: 5.2rem;
        }
    }

    &__stat-name {
        font-size: 1.5rem;
        text-align: center;
        color: #999999;
    }

    @include m.phone {
        &__stats {
            gap: 12rem;
        }
        
        &__stat-icon {
            max-width: 4.3rem;
        }
    
        &__stat-number {
            font-size: 9.5rem;
        }

        &__stat:nth-child(2) {
            .community__stat-icon {
                max-width: 3.5rem;
            }
    
            .community__stat-number {
                font-size: 8.3rem;
            }
        }

        &__stat-name {
            font-size: 2.2rem;
            text-align: left;
        }
    }

    @include m.tablet {
        &__stats {
            flex-direction: row;
            justify-content: space-around;
            align-items: center;
        }
    }

    @include m.desktopxl {
        &__stat-icon {
            max-width: 5.5rem;
        }
    
        &__stat-number {
            font-size: 11.5rem;
        }

        &__stat:nth-child(2) {
            .community__stat-icon {
                max-width: 4.7rem;
            }
    
            .community__stat-number {
                font-size: 11.5rem;
            }
        }

        &__stat-name {
            font-size: 2.9rem;
        }
    }
}