@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopxl {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}

@mixin desktopxxl {
    @media (min-width: v.$desktopxxl) {
        @content;
    }
}

@mixin ultra {
    @media (min-width: v.$ultra) {
        @content;
    }
}

/* Buttons */
@mixin btn {
    display: block;
    margin: 0 auto;
    border-radius: 25rem;
    padding: 1.4rem 4rem;
    width: fit-content;
    box-shadow: 0px 4px 6px 1px rgba(0,0,0,0.1);
    font-size: 1.2rem;
    font-weight: 700;
    background-color: v.$pink;
    color: v.$white;

    &:hover {
        background-color: v.$lightPink;
    }

    @include phone {
        padding: 2rem 6.6rem;
        font-size: 1.6rem;
    }

    @include desktop {
        padding: 2.7rem 9.2rem;
        font-size: 2rem;
        box-shadow: 0px 8px 15px 1px rgba(0,0,0,0.2);
    }

    @include desktopxxl {
        padding: 3.4rem 12rem;
        font-size: 2.4rem;
    }
}

/* Grid */
@mixin grid($columns, $spacing ) {
    display: grid;
    gap: $spacing;
    grid-template-columns: repeat($columns, 1fr);
}

/* Headings and paragraphs */
@mixin headingTwo {
    font-size: 2.4rem;
    line-height: 1.5;
    font-family: v.$secondaryFont;
    font-weight: 700;
    color: v.$cyan;
    text-align: center;

    @include phone {
        font-size: 3.2rem;
    }
    
    @include desktop {
        font-size: 4;
    }

    @include desktopxxl {
        font-size: 4.8rem;
    }
}

@mixin headingThree {
    font-size: 2rem;
    font-family: v.$secondaryFont;
    font-weight: 700;
    color: v.$cyan;
    text-align: center;

    @include phone {
        font-size: 3rem;
    }
    
    @include desktop {
        font-size: 4;
    }

    @include desktopxxl {
        font-size: 4.8rem;
    }
}

@mixin text {
    font-size: 1.4rem;
    line-height: 1.5;
    text-align: center;
    color: v.$cyan;

    @include phone {
        font-size: 1.6rem;
    }

    @include desktop {
        font-size: 1.8rem;
    }

    @include desktopxxl {
        font-size: 2rem;
    }
}