@use 'base/variables' as v;
@use 'base/mixins' as m;

.characteristics {
    &__audience,
    &__users {
        position: relative;
        padding: 8rem 0;
        background-color: v.$paleBlue;

        &::before {
            display: block;
            position: absolute;
            top: -17.5rem;
            left: 0;
            
            content: '';
            background-image: url(../img/bg-section-top-mobile-1.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: bottom center;
            height: 17.5rem;
            width: 100%;
        }

        &::after {
            display: block;
            position: absolute;
            bottom: -10.6rem;
            left: 0;
            
            content: '';
            background-image: url(../img/bg-section-bottom-mobile-1.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: top center;
            height: 10.6rem;
            width: 100%;
        }

        @include m.phone {
            &::before {
                background-position: top center;
                top: -16.5rem;
                background-size: cover;
                background-image: url(../img/bg-section-top-desktop-1.svg);
                height: 17.5rem;
            }
    
            &::after {
                bottom: -14rem;
                background-size: cover;
                background-image: url(../img/bg-section-bottom-desktop-1.svg);
                height: 19rem;

            }
        }

        @include m.desktop {
            padding: 13rem 0 9rem 0;
        }
    }

    &__audience {
        margin-top: 20rem;

        @include m.phone {
            margin-top: 30rem;
        }
    }

    &__meeting {
        margin: 19rem 0 22rem 0;
    }

    &__users {
        margin-top: 10rem;
        padding: 5rem 0 10rem 0;

        &::before {
            background-image: url(../img/bg-section-top-mobile-2.svg);
        }

        &::after {
            background-image: url(../img/bg-section-bottom-mobile-2.svg);
            bottom: -16rem;
            height: 16rem;
        }

        @include m.phone {
            &::before {
                background-image: url(../img/bg-section-top-desktop-2.svg);
            }
    
            &::after {
                background-image: url(../img/bg-section-bottom-desktop-2.svg);
                
            }
        }

        @include m.desktop {
            margin-top: 30rem;
            padding: 10rem 0;
        }
    }

    &__grid {
        @include m.desktop {
            display: grid;
            grid-template-columns: repeat(2,1fr);
            gap: 4rem;
            align-items: center;
        }
    }

    &__content {
        margin: 0 0 0 auto;
    }

    &__image {
        max-width: 82%;
        margin: 0 auto;

        @include m.phone {
            max-width: 60%; 
        }

        @include m.desktop {
            max-width: 88%;

            &--right {
                order: 1;
            }
        }
    }

    &__heading {
        margin-top: 7.8rem;
        @include m.headingThree;
        
        @include m.desktop {
            margin-top: 0;
            text-align: left;
        }
    }

    &__text {
        margin-top: 1.6rem;
        
        @include m.text;

        @include m.phone {
            margin: 2.4rem auto 0 auto;
            max-width: 50rem;
        }

        @include m.desktop {
            max-width: 65rem;
            text-align: left;
        }

        @include m.desktopxl {
            margin-top: 3.2rem;
        }
    }
}