.characteristics__audience, .characteristics__users {
  position: relative;
  padding: 8rem 0;
  background-color: hsl(207deg, 100%, 98%);
}
.characteristics__audience::before, .characteristics__users::before {
  display: block;
  position: absolute;
  top: -17.5rem;
  left: 0;
  content: "";
  background-image: url(../img/bg-section-top-mobile-1.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom center;
  height: 17.5rem;
  width: 100%;
}
.characteristics__audience::after, .characteristics__users::after {
  display: block;
  position: absolute;
  bottom: -10.6rem;
  left: 0;
  content: "";
  background-image: url(../img/bg-section-bottom-mobile-1.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top center;
  height: 10.6rem;
  width: 100%;
}
@media (min-width: 576px) {
  .characteristics__audience::before, .characteristics__users::before {
    background-position: top center;
    top: -16.5rem;
    background-size: cover;
    background-image: url(../img/bg-section-top-desktop-1.svg);
    height: 17.5rem;
  }
  .characteristics__audience::after, .characteristics__users::after {
    bottom: -14rem;
    background-size: cover;
    background-image: url(../img/bg-section-bottom-desktop-1.svg);
    height: 19rem;
  }
}
@media (min-width: 992px) {
  .characteristics__audience, .characteristics__users {
